@font-face {
  font-family: "Superstar";
  src: local("Superstar"),
    url("./fonts/Superstar.ttf") format("truetype");
  font-weight: normal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1A2340;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2{
  font-family: Superstar;
}

.App{
  display: flex;
  height: 100vh;
}
.col-left{
  width: 35%;
  padding-left: 5%;
}
.col-right{
  width: 65%;
  display: flex;
}

.game-window{
  width: 1300px;
  height: 857px;
    margin: auto;
    background: url('./images/GameBG.png') no-repeat 50%; 
    background-size: contain;
}

.game-window iframe{
  padding-top: 39px;
}
