.sec1{
  background: url('./images/optionsMenu.png') no-repeat 0%; 
  background-size: 450px;
  width: 450px;
  height: 432px;
  position: absolute;
  left: 70px;
}
.sec1::before{
  content: "Retro Arcade";
  font-family: Superstar;
  font-size: 32px;
    color: white;
    position: absolute;
    top: 44px;
    left: 102px;
}
.menuList{
  width: 311px;
  height: 230px;
  left: 21px;
  top: 117px;
    position: absolute;
}
.listItem{
  border: solid 1px #ffa50000;
  height: 25%;
  width: 100%;
    font-size: 18px;
    text-align: left;
    color: #978692;
}
.menuList h2{
  margin: 15px 55px;
}

.active{
  color: #F8FBC5;
}


.sec2{
  background: url('./images/upgradesMenu.png') no-repeat 0%; 
  background-size: 450px;
    width: 450px;
    height: 346px;
    position: absolute;
    left: 78px;
    top: 350px;
}
.sec2::before{
  content: "Featured Assets";
  font-family: Superstar;
  font-size: 32px;
    color: #6C72A2;
    position: absolute;
    top: 87px;
    left: 112px;
}
.sec3{
  background: url('./images/levelsMenu.png') no-repeat 0%; 
  background-size: 400px;
  width: 400px;
  height: 400px;
  position: absolute;
  left: 70px;
  bottom: 0px;
}
.sec3::before{
  content: "Games";
  font-family: Superstar;
  font-size: 32px;
    color: #6C72A2;
    position: absolute;
    left: 160px;
    bottom: 343px;
}

@media only screen and (max-width: 1672px) {
.game-window iframe{
  padding-top: 106px;
    width: 824px;
    height: 620px;
}
.game-window {
  width: 883px;
  height: 837px;
}
.sec1{
  left: 40px;
}
.sec3{
  left: 40px;
}
}

@media only screen and (max-width: 1400px) {
  .game-window iframe{
    padding-top: 169px;
    width: 665px;
    height: 501px;
  }
  .game-window {
    width: 713px;
    height: 837px;
  }
  .col-right{
    margin-right: 70px;
  }
  }